import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';

const resturl="https://restaurantapi0.herokuapp.com/restaurantdetails";
const placeorder="https://restaurantapi0.herokuapp.com/placeorder";

class PlaceOrder extends Component{
    constructor(){
        super()

        this.state={
            order_id:Number(Math.floor(Math.random()*1000)),
            rest_name:'',
            name:'',
            ph_no:'',
            address:'',
            people:''
        }
    }

    handleChangeName=(event)=>{
        this.setState({name:event.target.value})
    }
    handleChangePhone=(event)=>{
        this.setState({ph_no:event.target.value})        
    }
    handleChangePeople=(event)=>{
        this.setState({people:event.target.value})
    }
    handleChangeAddress=(event)=>{
        this.setState({address:event.target.value})
    }
    handleComplete=()=>{
        fetch(placeorder,{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(this.state)
        })
        .then((this.props.history.push('/viewBooking?message=Success')))
    }
    validatename=()=>{
        let name=document.getElementById('fname').value;
        if(name==' '||name==''){
            document.getElementById('nout').innerText="Please Enter Name"
        }else{
            document.getElementById('nout').innerText=""
        }
    }
    validatenumber=()=>{
        let phnumber=document.getElementById('number').value;
        if(phnumber==' '||phnumber==''){
            document.getElementById('pout').innerText="Please Enter Phone Number"
        }else{
            if(phnumber.match("^([0-9]{10})$")===null){
                document.getElementById('pout').innerText="Please Enter Valid Phone Number"
            }else{
            document.getElementById('pout').innerText=""
            }
        }
    }
    validateaddress=()=>{
        let add=document.getElementById('address').value;
        if(add==' '||add==''){
            document.getElementById('iout').innerText="Please Enter Address"
        }else{
            document.getElementById('iout').innerText=""
        }
    }
    validatepeople=()=>{
        let peep=document.getElementById('people').value;
        if(peep==' '||peep==''){
            document.getElementById('npout').innerText="Please Enter Number of People"
        }else{
            if(peep.match("^([0-9])$")===null){
                document.getElementById('npout').innerText="Please Enter a Number"
            }else{
            document.getElementById('npout').innerText=""
        }}
    }

    async componentDidMount(){
            let restid= this.props.match.params.id;
            let response= await axios.get (`${resturl}/${restid}`)
            this.setState({rest_name:response.data[0].name})
    }

    render(){
        console.log(this.props)
        return(
            <div className="container">
                <div className="panle panel-info">
                    <div className="panel-heading">
                        <h4>Place order</h4>
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label>Order_Id</label>
                            <input type="text" name="order_id" value={this.state.order_id} readOnly className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Restaurant Name</label>
                            <input type="text" name="rest_name" value={this.state.rest_name} readOnly className="form-control"/>
                        </div>
                        <div classname="form-group">
                            <label>Name</label>
                            <input type="text" name="name" value={this.state.name} className="form-control" onChange={this.handleChangeName} id="fname" onBlur={this.validatename}/>
                            <p className="color" id="nout"/>
                        </div>
                        <div className="form-group">
                            <label>Phone Number</label>
                            <input type="text" name="ph_no" value={this.state.ph_no} className="form-control" onChange={this.handleChangePhone} id="number" onBlur={this.validatenumber}/>
                            <p className="color" id="pout"/>
                        </div>
                        <div className="form-group">
                            <label>Address</label>
                            <input type="text" name="address" value={this.state.address} className="form-control" onChange={this.handleChangeAddress} id="address" onBlur={this.validateaddress}/>
                            <p className="color" id="iout"/>
                        </div>
                        <div className="form-group">
                            <label>Number of People</label>
                            <input type="text" name="people" value={this.state.people} className="form-control" onChange={this.handleChangePeople} id="people" onBlur={this.validatepeople}/>
                            <p className="color" id="npout"/>
                        </div>
                        <Link to={`/details/${this.props.match.params.id}`} className="btn btn-danger">Back</Link> &nbsp;
                        <button className="btn btn-success" onClick={this.handleComplete}>Complete Order</button>
                    </div>
                </div>
            </div>
        )
    }
}

export default PlaceOrder;