import React from 'react';
import {Link} from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import './RestaurantDisplay.css';

const RestaurantList=(props)=>{
    const renderRest=({resData})=>{
        if (resData){
            return(
                <div className="container">
                    <div className="DetailImage">
                    <img className="dimages" src={resData.thumb}/>
                        <button type="button" className="click" data-toggle="modal" data-target="#myModal">Click to open Image Gallery </button>
                        <div id="myModal" className="modal fade" role="dialog">
                            <div className="modal-dialog">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <button type="button" className="close" data-dismiss="modal">&times;</button>
                                        <h4 className="modal-title">Image Gallery</h4>
                                    </div>
                                    <div className="modal-body">
                                        <div id="myCarousel" class="carousel slide" data-ride="carousel">
                                           
                                            <ol class="carousel-indicators">
                                                <li data-target="#myCarousel" data-slide-to="0" class="active"></li>
                                            </ol>

                                            
                                            <div class="carousel-inner">
                                                <div class="item active">
                                                <img className="dimages"  className="img-responsive" src={resData.thumb}/>
                                                </div>
                                                <div class="item">
                                                <img className="dimages"  className="img-responsive" src={resData.image}/>
                                                </div>
                                            </div>
                                        </div>
                                        <a class="left carousel-control" href="#myCarousel" data-slide="prev">
                                            <span class="glyphicon glyphicon-chevron-left"></span>
                                            <span class="sr-only">Previous</span>
                                        </a>
                                        <a class="right carousel-control" href="#myCarousel" data-slide="next">
                                            <span class="glyphicon glyphicon-chevron-right"></span>
                                            <span class="sr-only">Next</span>
                                        </a>
                                    </div>
                                    <div className="modal-footer">
                                        <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                     <div className="dname">
                        {resData.name}
                    </div>
                    <hr/>
                    <Tabs>
                        <TabList className="dtabs">
                            <Tab>Overview </Tab>
                            <Tab>Contact</Tab>
                        </TabList>
                        <TabPanel>
                            <div className="dline1">
                            About this place
                            </div>    
                            <p><div className="dcuisine">
                                Cuisine
                            </div>
                            <div className="dtype">
                                {resData.Cuisine[0].name} , {resData.Cuisine[1].name}
                            </div></p>
                            <p><div className="dcost">
                                Average Cost
                            </div>
                            <div className="davgcost">
                                 ₹{resData.cost}
                            </div></p>
                        </TabPanel>
                        <TabPanel>
                            <div className="dphno">
                                Phone Number
                            </div>    
                            <div className="dno">
                               +91 {resData.contact_number}
                            </div>
                            <p><div className="dnamesmall">
                                {resData.name}
                            </div></p>
                            <p><div className="daddress">
                                <p>{resData.address}</p> 
                            </div></p>
                        </TabPanel>
                    </Tabs>
                    <div className="row">
                        <Link to={`/list/${sessionStorage.getItem('mealtype')}`} className="btn btn-danger">Back</Link>&nbsp;
                        <Link to={`/booking/${resData._id}`} className="btn btn-success">Place order</Link>
                    </div>
                </div>
               
            )
        }else {
            return(
            <img src="/images/805.gif"/>
            )}
    }
    return(
        <React.Fragment>
            {renderRest(props)}
        </React.Fragment>
    )
}
export default RestaurantList;