import React from 'react';
import {Link} from 'react-router-dom';
import './Listing.css';
import Pagination from 'react-js-pagination';

const ListingDisplay=(props)=>{
    const renderList=({listdata})=>{
        if(listdata){
            return listdata.map((item)=>{
                return(
                    <div className="element" key={item._id}>
                        <div classname="row">
                            <div className="col-md-5">
                                <img className="image" src={item.thumb}/>
                            </div>
                            <div className="col-md-7">
                                <div className="name">
                                    <Link to={`/details/${item._id}`} >{item.name}</Link>
                                </div>
                                <div className="info">
                                    {item.locality}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-sm-6">
                                <div className="cuisine">Cuisine: {item.Cuisine[0].name}| {item.Cuisine[1].name}</div>
                                <div className="cost">Cost: ₹{item.cost}</div>
                            </div>
                        </div>
                    </div>
                )
            })
        }else{
            return(
                <img src="/images/805.gif"/>
            )
        }
    }
    return(
        <div>
            <div className="row">
                <div className="col-md-12">
                    {renderList(props,props.listdata)}
                </div>
            </div>
        </div>
    )
}
export default ListingDisplay;