import React from 'react';
import './QuickSearch.css';
import {Link} from 'react-router-dom';

const QuickDisplay=(props)=>{

    const renderList=({quickData})=>{
        if(quickData){
            return quickData.map((item)=>{
                return(
                    <Link to={`/list/${item.mealtype}`}>
                        <div className="tilecontainer">
                            <div className="component1">
                                <img src={`/images/${item.name}.png`} style={{height:160, width:160}}/>
                            </div>
                            <div className="component2">
                                <div className="heading">{item.name}</div>
                                <div className="subheading">Start your day with exclusive {item.name} options</div>
                            </div>
                        </div>
                    </Link>
                )
            })
        }
    }
    return(
        <React.Fragment>
            <div className="container">
                <div className="quicksearch">
                    <p>Quick Search</p>
                </div>
                <div><p className="discover">Discover restaurants by type of meal</p>
                </div>
                {renderList(props)}
            </div>
        </React.Fragment>
    )
}
export default QuickDisplay;