import React,{Component} from 'react';
import './Search.css';
import {withRouter} from 'react-router-dom';

const lurl="https://restaurantapi0.herokuapp.com/location";
const rurl="https://restaurantapi0.herokuapp.com/restaurant?city=";

class Search extends Component{
    constructor(){
        super()

        this.state={
            location:'',
            restaurants:''
        }
    }
    renderCity=(data)=>{
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item.city}>
                        {item.city_name}
                    </option>
                )
            })
        }
    }
    renderRestaurant=(data)=>{
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item._id}>
                        {item.name} | {item.locality}
                    </option>
                )
            })
        }
    }
    handleCity=(event)=>{
        const cityid= event.target.value
        const rrurl=`${rurl}${cityid}`
        fetch(rrurl, {Method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{this.setState({restaurants:data})})
    }
handleRest=(event)=>{
    this.props.history.push(`/details/${event.target.value}`)
}

    render(){
        console.log(this.state.location);
        return(
            <div className="imagecontainer">
                <div className="logo">
                    d!
                </div>
                <div className="line1">
                    Find the best restaurants, cafes and bars
                </div>
                <div className="locationSelector">
                    <select className="locationDropDown" onChange={this.handleCity}>
                        <option>----Select City----</option>
                        {this.renderCity(this.state.location)}
                    </select>
                    <select className="locationDropDown1" onChange={this.handleRest}>
                        <option>----Select Restaurants----</option>
                        {this.renderRestaurant(this.state.restaurants)}
                    </select>   
                </div>
            </div>
        )
    }
    componentDidMount(){
        fetch(lurl, {Method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{this.setState({location:data})})
    }
}

export default withRouter (Search);