import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './Header.css'

const Header=()=>{
    return(
        <React.Fragment>
            <nav className="navbar navbar-custom">
                <div className="container-fluid">
                    <div className="navbar-header">
                        <Link className="navbar-brand" to="/">FoodDev</Link>
                    </div>
                    <ul className="nav navbar-nav">
                        <li><Link to="/" className="house">Home</Link></li>
                        <li><Link to="/viewbooking" className="house">Orders</Link></li>
                    </ul>
                </div>
            </nav>
        </React.Fragment>
    )
}

export default Header;