import React,{Component} from 'react';
import axios from 'axios';
import './Filter.css';

let sorturl='https://restaurantapi0.herokuapp.com/restaurantlist';

class SortFilter extends Component{
    sortfilter=(event)=>{
        let sorttype=Number(event.target.value);
        let mealType=sessionStorage.getItem('mealtype');
        let outurl
        outurl=`${sorturl}/${mealType}?sort=${sorttype}`
        axios.get(outurl)
        .then((response)=>{this.props.sortdata(response.data)})
    }
    render(){
        return(
            <React.Fragment>
                <center className="top">Sort</center>
                <div onChange={this.sortfilter}>
                    <label className="radio">
                        <input type="radio" value="1" name="filter"/>Low to High
                    </label>
                    <label className="radio">
                        <input type="radio" value="-1" name="filter"/>High to Low
                    </label>
                </div>
            </React.Fragment>
        )
    }
}

export default SortFilter;