import React,{Component} from 'react';
import axios from 'axios';
import './Filter.css';

const costurl='https://restaurantapi0.herokuapp.com/restaurantlist';

class CostFilter extends Component{
    costfilter=(event)=>{
        let cost=(event.target.value).split(',');
        let lcost=Number(cost[0]);
        let hcost=Number(cost[1]);
        let mealType=sessionStorage.getItem('mealtype');
        let curl;
        if(cost===''){
            curl=`${costurl}/${mealType}`
        }else{
            curl=`${costurl}/${mealType}?hcost=${hcost}&lcost=${lcost}`
        }
        axios.get(curl)
        .then((response)=>{this.props.costdata(response.data)})
    }
    render(){
        return(
            <React.Fragment>
                <center className="top">Cost Filter</center>
                <div onChange={this.costfilter}>
                    <label className="radio">
                        <input type="radio" value="" name="filter"/>All
                    </label>
                    <label className="radio">
                        <input type="radio" value="100,300" name="filter"/>100-300
                    </label>
                    <label className="radio">
                        <input type="radio" value="301,500" name="filter"/>301-500
                    </label>
                    <label className="radio">
                        <input type="radio" value="501,700" name="filter"/>501-700
                    </label>
                </div>
            </React.Fragment>
        )
    }
}

export default CostFilter;