import React from 'react';
import {BrowserRouter,Route} from 'react-router-dom';
import Home from './Home/Home';
import RestaurantDetails from './Detail/RestaurantDetails';
import RestaurantList from './Detail/RestaurantDisplay';
import Header from './Header';
import Footer from './Footer';
import RestListing from './Listing/ListingApi';
import Bookings from './Orders/BookingsApi';
import Placeorder from './Orders/BookingForm';

const Routing=()=>{
    return(
        <BrowserRouter>
            <div>
                <Header/>
                <Route exact path="/" component={Home}></Route>
                <Route path="/list/:id" component={RestListing}></Route>
                <Route path="/details/:id" component={RestaurantDetails}></Route>
                <Route path="/viewbooking" component={Bookings}></Route>
                <Route path="/booking/:id" component={Placeorder}></Route>
                <Footer/>
            </div>
        </BrowserRouter>
    )
}
export default Routing;