import React,{Component} from 'react';
import axios from 'axios';
import ListingDisplay from './ListingDisplay';
import CostFilter from '../Filter/Cost';
import CuisineFilter from '../Filter/Cuisine';
import SortFilter from '../Filter/Sort';

const url="https://restaurantapi0.herokuapp.com/restaurant?mealtype=";

class ListingApi extends Component{
    constructor(props){
        super()

        this.state={
            restlist:''
        }
    }

    setDataAsPerFilter(sortedData){
        this.setState({restlist:sortedData})
    }
    render(){
        return(
            <div className="container">
                <div className="row">
                    <div className="col-md-2">
                       <CostFilter costdata={(data)=>{this.setDataAsPerFilter(data)}}/>
                       <CuisineFilter cuisinedata={(data)=>{this.setDataAsPerFilter(data)}}/>
                       <SortFilter sortdata={(data)=>{this.setDataAsPerFilter(data)}}/>
                    </div>
                    <div className="col-md-10">
                        <ListingDisplay listdata={this.state.restlist}/>
                    </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
        var mealtype=this.props.match.params.id
        sessionStorage.setItem('mealtype', mealtype)
        axios.get(`${url}${mealtype}`)
        .then((response=> {this.setState({restlist:response.data})}))
    }
}
export default ListingApi;